.page {
  font-family: "Quicksand", sans-serif;
  color: #f9f9f9;
  text-align: center;
  margin: 0px auto 0px auto;
  padding: 4%;
  min-height: 100vh;
  background: #da4453; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #89216b,
    #da4453
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #89216b,
    #da4453
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.typing {
  font-size: 50px;
}

.aboutme {
  color: #f9f9f9;
}
.hello {
  text-transform: uppercase;
  font-size: 40px;
  font-family: "Quicksand", sans-serif;
  color: #f9f9f9;
}

.social-media-list {
  margin: 30px;
}
.social-media-list a {
  margin: 0.5rem;
}

@media (max-width: 999px) {
  .hello {
    font-size: 24px;
    padding-bottom: 50px;
  }
  .social-media-list {
    padding: 20% 0;
    margin: 0;
  }

  .skills {
    font-size: 30px;
    padding: 2em 0;
  }
  .box-aboutme {
    margin: 4em 1em;
  }
  .img {
    width: 70%;
    height: 60%;
  }
  .typing {
    font-size: 20px;
  }
}

@media (min-width: 1290px) {
  .hello {
    font-size: 80px;
    padding: 0.5em;
  }

  .aboutme {
    font-size: 50px;
  }

  .social-media-list {
    padding: 5em;
  }
}

@media (min-width: 1024px) {
  .typing {
    font-size: 24px;
  }
}
