@import url("https://fonts.googleapis.com/css?family=Quicksand");

p {
  text-align: center;
  font-weight: 500;
}

.container {
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
}

.contact-form {
  margin: 4em;
}
.section {
  background-color: #232323;
  color: white;
  font-weight: 800;
  height: 100vh;
  font-size: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.aboutme-text {
  width: 70%;
  padding: 4em 0;
  font-weight: 800;
  display: flex;
  justify-content: center;
}
.aboutme {
  clip-path: polygon(0 0, 100% 0, 100% 75%, 0 100%);
  display: flex;
  justify-content: center;
  color: white;
  height: 100vh;
  background: #da4453; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #89216b,
    #da4453
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #89216b,
    #da4453
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.about img {
  width: 50%;
  height: 100%;
  border-radius: 8px;
  box-shadow: 0.4rem 0.2rem 0.4rem 0.4rem rgba(46, 41, 41, 0.1);
  margin: 10px auto;
}

.links {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.links a {
  padding: 0 2rem;
}
* {
  font-family: "Quicksand", sans-serif;
  font-weight: 800;
}

.photo {
  border-radius: 25px;
  padding: 20px;
}

body {
  width: 100vw;
  height: 100vh;
  letter-spacing: 0.1em;
  position: relative;
}

.box .pics {
  width: 100%;
  opacity: 0.9;
}

.box .pics:hover {
  opacity: 1;
}

.icon {
  padding-bottom: 80px;
}

.content-aboutme {
  background: #da4453; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #89216b,
    #da4453
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #89216b,
    #da4453
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.form {
  text-transform: uppercase;
}
.getintouch {
  margin: auto;
  font-size: 30px;
  padding: 2em;
  text-transform: uppercase;
}
.links img {
  width: 100px;
  height: 100px;
}

.skills {
  justify-content: center;
  text-align: center;
  font-size: 40px;
  padding-bottom: 3%;
  font-weight: 700;
  color: black;
}
.technology {
  background-color: #ba365c;
  color: #f9f9f9;
  padding: 1rem 1rem;
  border-radius: 3px;
  display: inline-block;
  margin-right: 10px;
  margin-left: 10px;
  margin-top: 20px;
  margin-bottom: 50px;

  text-align: center;
}

.box:hover {
  margin-top: 70px;
}

.box-aboutme {
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0.4rem 0.2rem 0.4rem 0.4rem rgba(46, 41, 41, 0.1);
  display: block;
  padding: 40px;
  margin: 100px;
  text-align: center;
  transition: all 0.3s;
}
.box {
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0.4rem 0.2rem 0.4rem 0.4rem rgba(46, 41, 41, 0.1);
  color: #293347;
  display: block;
  padding: 2em;
  margin: 60px;
  text-align: center;
  transition: all 0.3s;
  height: 90%;
}

.sendbutton {
  width: 50%;
}
.check-code {
  cursor: pointer;
  text-decoration: none;
  border-radius: 3px;
  color: #f9f9f9;
  background: #ba365c;
  text-transform: uppercase;
  padding: 1rem 2rem;
  margin: 20px;
  transition: all 0.3s;
}

.check-code:hover,
.check-code:active {
  color: #f9f9f9;
}
@media (max-width: 999px) {
  .footer {
    position: relative;
    right: 0;
    bottom: 0;
    left: 0;
  }

  body {
    margin: auto 0;
    min-height: 100%;
  }
  .icon {
    display: none;
  }

  .photo {
    padding: 0px;
    border-radius: 0px;
  }

  html {
    height: 100%;
    box-sizing: border-box;
  }
  *,
  *:before,
  *:after {
    box-sizing: inherit;
  }

  .about {
    text-align: center;
    font-size: 20px;
    line-height: 1.5em;
    padding: 20px;
  }

  .aboutme-text {
    padding: 3em 0.5em;
    width: 100%;
    font-size: 26px;
  }

  .box-aboutme {
    padding: 1em;
    margin: 1em;
  }

  .links a {
    padding: 0;
  }

  .technology {
    background-color: #ba365c;
    color: #f9f9f9;
    border-radius: 3px;
    display: inline-block;
    padding: 1em;
    margin: 0.2em;
    text-align: center;
  }

  .box {
    padding: 2m;
    margin: 2em;
  }
}

@media (min-width: 1025px) and (max-width: 1440px) {
  .box-aboutme {
    padding: 5em;
  }

  .skills {
    font-size: 70px;
  }

  .technology {
    padding: 1em 2em;
  }

  .title {
    font-size: 30px;
  }

  .code {
    padding: 2em;
  }

  .getintouch {
    font-size: 40px;
    font-weight: 900;
    letter-spacing: 0.5em;
  }
  .contactdetails {
    font-size: 30px;
  }
  .myemail {
    font-size: 20px;
  }

  .aboutme-text {
    padding: 2em 0;
    font-size: 32px;
  }
}

@media (min-width: 768px) and (max-width: 1023px) {
  .aboutme-text {
    padding: 2em 0;
    font-size: 24px;
  }

  .code {
    padding: 2em;
  }
}

@media (min-width: 1024px) and (max-width: 1366px) {
  .aboutme-text {
    font-size: 32px;
  }
  .skills {
    padding: 2em;
    font-size: 40px;
  }

  .technology {
    padding: 1em;
  }

  .box {
    padding: 2em;
    margin: 2em 1em;
  }
}

@media (min-width: 320px) and (max-width: 480px) {
  .box {
    padding: 1em;
    height: 100%;
  }
  .code {
    padding: 1em;
  }

  .aboutme-text {
    font-size: 20px;
  }
}

@media (min-width: 320px) and(max-width: 568px ) {
  .aboutme-text {
    font-size: 20px;
  }
}

@media (min-width: 375px) and (max-width: 667px) {
  .aboutme-text {
    font-size: 23px;
  }
}

@media (min-width: 668px) and (max-width: 1020px) {
  .toolbar__navigation ul {
    font-size: 10px;
  }
}
