    to(#da4453)
  );
  background: -webkit-linear-gradient(
    left,
    #89216b,
    #da4453
  );
  background: linear-gradient(
    to right,
    #89216b,
    #da4453
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.typing {
  font-size: 50px;
}

.aboutme {
  color: #f9f9f9;
}
.hello {
  text-transform: uppercase;
  font-size: 40px;
  font-family: "Quicksand", sans-serif;
  color: #f9f9f9;
}

.social-media-list {
  margin: 30px;
}
.social-media-list a {
  margin: 0.5rem;
}

@media (max-width: 999px) {
  .hello {
    font-size: 24px;
    padding-bottom: 50px;
  }
  .social-media-list {
    padding: 20% 0;
    margin: 0;
  }

  .skills {
    font-size: 30px;
    padding: 2em 0;
  }
  .box-aboutme {
    margin: 4em 1em;
  }
  .img {
    width: 70%;
    height: 60%;
  }
  .typing {
    font-size: 20px;
  }
}

@media (min-width: 1290px) {
  .hello {
    font-size: 80px;
    padding: 0.5em;
  }

  .aboutme {
    font-size: 50px;
  }

  .social-media-list {
    padding: 5em;
  }
}

@media (min-width: 1024px) {
  .typing {
    font-size: 24px;
  }
}

.side-drawer {
  height: 100%;
  background: white;
  box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 70%;
  max-width: 400px;
  z-index: 200;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  -webkit-transition: -webkit-transform 0.4s ease-in-out;
  transition: -webkit-transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
}

.side-drawer.open {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.side-drawer ul {
  height: 70%;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.side-drawer li {
  margin: 0.5rem 0;
  margin: 2em;
}

.side-drawer a {
  color: #a22c64;
  text-transform: uppercase;
}

.side-drawer a:hover,
.side-drawer a:active {
  color: black;
}

@media (min-width: 769px) {
  .side-drawer {
    display: none;
  }
}

@media (max-width: 999px) {
  .backdrop {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    height: 100%;
    background: rgba(0, 0, 0, 0.3);
    z-index: 100;
  }
}

.toggle-button {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 34px;
  width: 30px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  box-sizing: border-box;
}

.toggle-button:focus {
  outline: none;
}

.toggle-button__line {
  width: 35px;
  height: 3px;
  background: white;
}

.toolbar {
  width: 100vw;
  background: #da4453; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #89216b,
    #da4453
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#89216b),
    to(#da4453)
  );
  background: -webkit-linear-gradient(
    left,
    #89216b,
    #da4453
  );
  background: linear-gradient(
    to right,
    #89216b,
    #da4453
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.toolbar__navigation {
  padding: 1em;
  height: 100%;

  align-items: center;
}

.toolbar_navigation-items ul {
  list-style: none;
  margin: 0;
  padding: 1em;
  display: flex;
  justify-content: center;
}

.toolbar_navigation-items li {
  padding: 0 2rem;
  text-transform: uppercase;
}

.toolbar_navigation-items a {
  color: white;
  text-decoration: none;
  font-size: 30px;
}

.toolbar_navigation-items a:hover,
.toolbar_navigation-items a:active {
  color: black;
  background-color: white;
}

@media (max-width: 768px) {
  .toolbar_navigation-items {
    display: none;
  }
}

@media (min-width: 769px) {
  .toolbar__toggle-button {
    display: none;
  }
}

